const test = require('ava');
const isBalanced = require('./is-balanced');

test(`Level 1 Test`, (t) => {
  const src = '{[()]}';
  const result = isBalanced(src);
  const expected = true;

  t.is(result, expected, 'Fail Level 1 Test');
});

test(`Level 2 Test`, (t) => {
  const src = '{[()[]]({}[({}[])])}';
  const result = isBalanced(src);
  const expected = true;

  t.is(result, expected, 'Fail Level 2 Test');
});

test(`Level 3 Test`, (t) => {
  const src = '(function test(valid) {if(valid) {return ((1 + 2) > (true || false));}return [1, 2, 3].filter((i) => {return (i > 1);});})(true);';
  const result = isBalanced(src);
  const expected = true;

  t.is(result, expected, 'Fail Level 3 Test');
});
